        case 3: return 'RP4i1  '
        case 4: return 'RP4i1 UVauz'
        case 5: return 'RP4i1 JtrJi '
      }
    },
    submitEnabled () {
      let t = this
      return t.login.length > 0 && t.password.length >= 6
    }
  },
  beforeMount () {
    let t = this
    t.initiatePage()
    t.cycleHandler = setInterval(t.cycleScreenshots, 5000);
  },
  beforeDestroy () {
    let t = this
    clearInterval(t.cycleHandler)
  }
};
</script>

